// src/router/index.js
import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  }
]

const router = createRouter({
  // Substitua 'NOME_DO_SEU_REPOSITORIO' pelo nome do seu reposit√≥rio no GitHub
  history: createWebHistory('/equilibriomatinal/'), 
  routes
})

export default router
